@import 'tui-pagination/dist/tui-pagination.css';
@charset 'utf-8';

.tui-pagination * {
  -moz-user-select: none;
  -webkit-user-select: none;
  -ms-user-select: none;
  user-select: none;
}

.tui-ico-ellipsis,
.tui-ico-first,
.tui-ico-last,
.tui-ico-next,
.tui-ico-prev {
  overflow: hidden;
}


.tui-pagination {
    height: 28px;
    margin: 0;
  
    line-height: 1.33;
    text-align: center;
    font-size: 12;

    
    @media screen and (min-width: 768px){
      height: 40px;
    }
}

.tui-pagination .tui-page-btn {
  display: inline-block;
  position: relative;
  width: 28px;
  height: 28px;
  padding-top: 8px;
  margin-left: 0;

  color: var(--main-text-color);
  font-size: 12px;
  font-weight: normal;
  line-height: 1.33;
  text-decoration: none;
  vertical-align: middle;
  text-align: center;

  border: transparent;
  border-radius: 5px;

  @media screen and (min-width: 768px){
        width: 40px;
          height: 40px;
          padding-top: 12px;
    }
 
}

// .tui-pagination .tui-page-btn:hover {
//     background-color: var(--btn-hover-background);
// }

.tui-pagination .tui-is-selected,
.tui-pagination strong {
  color: #fff;
  background: var(--btn-hover-background);
  border-color: transparent;
  cursor: default;
}

.tui-pagination .tui-is-selected:hover {
  background-color: var(--btn-hover-background);
}

.tui-pagination .tui-first-child.tui-is-selected,
.tui-pagination strong.tui-first-child {
  margin-left: 0;
  background: var(--btn-hover-background);
  border-color: var(--btn-hover-background);
}

.tui-pagination .tui-first-child,
.tui-pagination .tui-prev-is-ellip {
  border: transparent;
}

.tui-pagination .tui-last-child,
.tui-pagination .tui-next-is-ellip {
  border: transparent;
}

.tui-pagination .tui-first-child.tui-is-selected {
  border: transparent;
}

.tui-pagination .tui-last-child.tui-is-selected {
  // border-right: 1px solid #4b96e6
  border: transparent;
}

.tui-pagination .tui-first,
.tui-pagination .tui-prev,
.tui-pagination .tui-next,
.tui-pagination .tui-last,
.tui-pagination .tui-prev-is-ellip,
.tui-pagination .tui-next-is-ellip {
  overflow: hidden;
  height: 28px;
  padding: 0;
  margin: 0;
  font-size: 12;
  line-height: 1.33px;
  white-space: nowrap;
  font-weight: normal;
  border: transparent;

  @media screen and (min-width: 768px){
    height: 40px;
  }
}

.tui-pagination .tui-prev-is-ellip {
  border-right: 0;
}

.tui-pagination .tui-next-is-ellip {
  border-left: 0;
}

.tui-pagination .tui-prev,
.tui-pagination .tui-next {
  border-left: 0;
}

// .tui-pagination .tui-first,
// .tui-pagination .tui-last {
//     visibility: hidden;
//         opacity: 0;
//         pointer-events: none;
// }

.tui-pagination .tui-ico-first,
.tui-pagination .tui-ico-prev,
.tui-pagination .tui-ico-next,
.tui-pagination .tui-ico-last,
.tui-pagination .tui-ico-ellip {
  background: url(data:image/png;base64,)
    no-repeat 0 0;
  display: inline-block;
  vertical-align: middle;
  font-size: 0px;
  margin-top: 10px;

  @media screen and (min-width: 768px) {
      margin-top: 15px;
    }
}

.tui-pagination .tui-prev-is-ellip,
.tui-pagination .tui-next-is-ellip {
  padding: 0;
  line-height: 1;
}

.tui-pagination .tui-prev {
  margin-right: 0;
}

.tui-pagination .tui-next {
  margin-left: 0;
}

.tui-pagination .tui-ico-first {
  width: 11px;
  height: 10px;
  background-position: 0px 0px;

}

.tui-pagination .tui-ico-prev {
  width: 6px;
  height: 10px;
  background-position: -26px 0px;
}

.tui-pagination .tui-ico-next {
  width: 6px;
  height: 10px;
  background-position: -13px -24px;
}

.tui-pagination .tui-ico-last {
  width: 11px;
  height: 10px;
  background-position: 0px -12px;
}

.tui-pagination .tui-ico-ellip {
  width: 10px;
  height: 2px;
  margin-top: 14px;
  vertical-align: middle;
  background-position: 0px -24px;


  @media screen and (min-width: 768px) {
      margin-top: 20px;
    }
}

.tui-pagination .tui-is-disabled:hover {
  background: #fff;
  cursor: default;
}

.tui-pagination .tui-is-disabled .tui-ico-prev {
  background-position: -26px -12px;
}

.tui-pagination .tui-is-disabled .tui-ico-first {
  background-position: -13px 0px;
}

.tui-pagination .tui-is-disabled .tui-ico-next {
  background-position: -21px -24px;
}

.tui-pagination .tui-is-disabled .tui-ico-last {
  background-position: -13px -12px;
}

